<template>
  <div class="page flex-v">
    <div class="head flex-v-center flex-justify">
      <el-breadcrumb separator-class="el-icon-arrow-right"
        class="my-breadcrumb">
        <el-breadcrumb-item :to="{ name: 'SetMealList' }">套餐维护</el-breadcrumb-item>
        <el-breadcrumb-item>查看</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
      <el-form inline
        size='small'
        class="default-form-style">
        <el-row>
          <el-col :span="6">
            <el-form-item label="套餐名称：">
              {{dataInfo.packName}}
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="套餐编号：">
              {{dataInfo.sysGuid}}
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="套餐顺序：">
              {{dataInfo.sortNum}}
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="拼音简码：">
              {{dataInfo.pySimpleCode}}
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="五笔简码：">
              {{dataInfo.wbSimpleCode || '-'}}
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="是否启用：">
              {{dataInfo.enabled == 1 ? '是' : '否'}}
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div class="table-box">
        <h4>已选项目</h4>
        <el-table :data='projectList'
          header-cell-class-name='custom-table-cell'>
          <el-table-column label='项目名称'
            prop="pname"
            align='center'></el-table-column>
          <el-table-column label='小项名称'
            prop='smallProjects'
            min-width="240px"
            show-overflow-tooltip
            align='center'></el-table-column>
        </el-table>
      </div>
      <div v-if="hasUpdate"
        data-btn-id='106007002'>
        <el-button class="btn-info"
          @click="$router.push({name: 'SetMealForm', params: { id }})">修改</el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SetMealSee',
  props: {
    id: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      dataInfo: {},
      projectList: []
    }
  },
  computed: {
    hasUpdate() {
      return ~this.$store.state.btnAuthorityIds.indexOf('106007002')
    }
  },
  created() {
    this.getSetMealById(this.id)
  },

  methods: {
    getSetMealById(sysGuid) {
      this.$post('/sys/projectPackage/getBySysGuid', { sysGuid })
        .then(({ data }) => {
          this.dataInfo = data.packBasic
          let projects = data.thisOrgAllProjects,
            packChoice = data.packChoice.map(item => item.projectId)
          this.projectList = projects
            .filter(item => {
              return item.smallProjects.some(item => ~packChoice.indexOf(item.sysGuid))
            })
            .map(item => {
              let smallProjects = item.smallProjects
                .filter(items => ~packChoice.indexOf(items.sysGuid))
                .map(item => item.pname)
              let newItem = {
                pname: item.pname,
                smallProjects: smallProjects.join('、')
              }
              return newItem
            })
        })
        .catch(err => err)
    }
  }
}
</script>

<style lang="less" scoped>
.head {
  padding: 0 70px 0 20px;
  height: 70px;
  border-bottom: 1px solid #eaeff5;
}

.container {
  padding: 30px;
  overflow-y: auto;
  .table-box {
    padding-left: 0;
    width: 680px;
    & + div {
      margin-top: 60px;
      text-align: center;
    }
  }
  h4 {
    margin: 20px 0;
    line-height: 1;
    font-size: 14px;
  }
}
</style>


